@font-face {
  font-family: 'Roboto Mono';
  font-style: normal;
  font-weight: 400;
  src: url(/sketch/static/media/RobotoMono-Regular.a3649815.ttf);
  src: local('Roboto Mono'), local('RobotoMono'),
       url(/sketch/static/media/RobotoMono-Regular.a3649815.ttf?#iefix) format('truetype'),
}

body, input, textarea, select, button {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Mono', 'DejaVu Sans Mono', monospace;
  letter-spacing: -0.48px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

